/* -- HEADER -- */
//.header

.header__logo {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
}

.header__nav {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 5;

        flex-direction: column;
        justify-content: flex-start;

        width: 100%;
        height: 100vh;

        padding-top: 136px;
        padding-right: 45px;
        padding-left: 45px;

        background-color: $mobile_nav_bg;
    }
}

// SEARCH
.header__search-form {
    position: relative;

    width: 50%;
    padding-top: 43px;

    &::before {
        position: absolute;

        bottom: 5px;
        left: 0;

        width: 14px;
        height: 14px;

        content: "";

        background-image: url("../img/search.svg");
    }

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        width: 100%;
        padding-top: 0;
        margin-bottom: 45px;

        &::before {
            bottom: 12px;
            left: 12px;

            width: 16px;
            height: 16px;

            background-image: url("../img/search_white.svg");
        }
    }
}

.header__search-form input {
    padding-left: 30px;

    font-size: 14px;
    line-height: 21px;

    border: none;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        width: 100%;
        height: 40px;
        padding-left: 50px;

        background-color: rgba(255, 255, 255, 0.15);
        border-radius: 21px;

        &::placeholder {
            color: transparent;
        }
    }
}

// USER MENU
.header__user-menu {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;

    width: 50%;
    padding: 0;
    padding-top: 43px;
    margin: 0;
    margin-bottom: 51px;

    font-size: 14px;
    line-height: 21px;

    list-style: none;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        order: 4;
    }
}

.header__user-menu li {
    margin-left: 35px;
}

.header__user-menu a {
    color: $text_color;

    &:hover {
        opacity: $hover_opacity;
    }

    &:active {
        opacity: $active_opacity;
    }

    transition: $standart_transition;
}


// HEADER MENU
.header__menu {
    width: 100%;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        order: 2;
    }
}

.header__menu ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;


    min-height: 61px;
    padding: 0;
    margin: 0;
    margin-left: -20px;

    list-style: none;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        margin-left: 0;
    }
}

.header__menu li {
    margin-left: 35px;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        &:not(:last-child) {
            margin-bottom: 21px;
        }

        margin-left: 0;
    }
}

.header__menu a {
    color: $text_color;

    &:hover {
        opacity: $hover_opacity;
    }

    &:active {
        opacity: $active_opacity;
    }

    transition: $standart_transition;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        font-size: 24px;
        line-height: 36px;
        color: $light_text_color;
    }
}

// ABOUT MENU
.header__about-menu {
    display: none;

    /* -- TABLET -- */
    @media (max-width: $tablet_max_width) {
        order: 3;
    }
}

